 #000,inset 0 -2px 5px #000,inset 0 2px 2px rgba(255,255,255,0.5),0 0 0 2px #000,0 0 0 5px #0c0c0c,0 0 0 5.5px #080808}label input[type="checkbox"]:checked ~ .btn{box-shadow:0 0 0 #000,inset 0 -2px 5px #000,inset 0 1px 1px rgba(255,255,255,0.1),0 0 0 2px #000,0 0 0 5px #0c0c0c,0 0 0 5.5px orange}label input[type="checkbox"]{-webkit-appearance:none}label .fa{position:absolute;top:50px;left:50px;transform:translate(-143%, -143%);font-size:2em;color:#0c0c0c}label input[type="checkbox"]:checked ~ .fa{color:orange;text-shadow:0 0 15px orange, 0 0 30px orange}.banker{margin:0;padding:0}.center{position:relative;margin-top:20px;top:50%;left:50%;transform:translate(-50%, -50%)}input[name="bank"]{cursor:pointer;position:relative;width:40px;height:20px;-webkit-appearance:none;background:#111111;outline:none;border-radius:10px;box-shadow:inset 0 0 5px rgba(0,0,0,0.2);transition:0.5s}input:checked[name="bank"]{background-color:orange}input[name="bank"]::before{content:"";position:absolute;width:20px;height:20px;border-radius:10px;top:0;left:0;background:orange;transform:scale(1.1);box-shadow:0 2px 5px rgba(0,0,0,0.2);transition:0.5s}input:checked[name="bank"]::before{left:20px;background:#000}input[type="range"]{width:200px;height:2px;background:#000;-webkit-appearance:none;outline:none;border-radius:2px}input[type="range"]::-webkit-slider-thumb{-webkit-appearance:none;width:20px;height:20px;border-radius:50%;background:orange}.footer{width:100%;text-align:center;display:block;margin-top:50px;position:absolute;font-size:0.9em;color:#fff}.footer button{font-family:"Dosis", sans-serif;padding:0.5em;font-size:1.2em;background-color:transparent;font-weight:100;font-size:1.3em;border:2px solid;border-color:#fff;border-radius:10px;color:#fff}.footer button:hover{background-color:#f39c12;font-weight:200;border:none;color:#333;cursor:pointer;transform:scale(1.1);box-shadow:0 0 10px #f39c12, 0 0 40px #f39c12, 0 0 80px #f39c12}@media screen and (max-width: 650px){.inner-container{transform:scale(0.7)}}

